import AreaSegment from '../area-chart/area-segment';
import LineSegment from '../line-chart/line-segment';

class PolarAreaSegment extends AreaSegment {
    points() {
        const { parent: { plotArea: { polarAxis } }, stackPoints } = this;
        const center = polarAxis.box.center();
        const points = LineSegment.prototype.points.call(this, stackPoints);

        points.unshift([ center.x, center.y ]);
        points.push([ center.x, center.y ]);

        return points;
    }
}

export default PolarAreaSegment;